import React, { useEffect } from "react";
import cls from "../Peed.module.scss";
import { FaHeart } from "react-icons/fa";
import axios from "axios";
import { useDispatch } from "react-redux";
import { editFeed } from "../../../../redux/reducer/FeedReducer";
const FeedSelect = ({ m, slide }) => {
  const dispatch = useDispatch();

  const postLike = async (v) => {
    try {
      const res = await axios.post("/api/data/likeFeed", {
        _id: m._id,
      });

      if (m.status) {
        dispatch(
          editFeed({
            likeCount: m.likeCount - 1,
            like: !m.status,
            slide,
            _id: m._id,
          })
        );
      } else {
        dispatch(
          editFeed({
            likeCount: m.likeCount + 1,
            like: !m.status,
            slide,
            _id: m._id,
          })
        );
      }
    } catch (err) {}
  };

  useEffect(() => {}, [slide, dispatch]);
  return (
    <div className={cls.profile__wrapper}>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          position: "absolute",
          top: "10px",
          left: "10px",
        }}
      >
        <div
          style={{
            width: "fit-content",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            backgroundColor: "#cecece",
            marginRight: "5px",
            borderRadius: "50%",
          }}
        >
          <img
            src={m.img}
            alt={m.nickname}
            style={{
              width: "30px",
              height: "30px",
              borderRadius: "50%",
            }}
          />
        </div>
        <h3>{m.nickname}</h3>
      </div>
      <button
        style={{
          position: "absolute",
          bottom: "10px",
          right: "15px",
          backgroundColor: "transparent",
          border: "none",
        }}
        value={m._id}
        onClick={postLike}
      >
        <FaHeart className={m.status ? `${cls.heart} ${cls.red}` : cls.heart} />
      </button>
      <div style={{ position: "absolute", bottom: "10px", right: "35px" }}>
        {m.likeCount}
      </div>
      {m.answer.index === slide ? (
        <div className={cls.answer__wrapper} key={m.answer.index}>
          <p>{m.answer.answer}</p>
        </div>
      ) : (
        ""
      )}
    </div>
  );
};

export default FeedSelect;
